import PutTable from "../../../../tools/Table/PutTable";
import { step2rows } from "../../../../inputs/Step2/step2data";
import { theme } from "../../../../forms/Theme";
import {
  Typography,
  Accordion,
  AccordionSummary,
  AccordionDetails,
} from "@mui/material";
import { Box } from "@mui/system";
import { useStyles } from "./Step2Styles";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";

const acors = [
  {
    Head: "1 - Consignation électrique",
    passN: true,
    extended: true,
  },
  {
    Head: "3 - Purge du circuit et de l’espace",
    passN: false,
  },
  {
    Head: "4 - Isolation",
    passN: false,
  },
  {
    Head: "5 - Aération/Ventilation",
    passN: false,
  },
  {
    Head: "6 - ATEX; Permis de feu / fouille; autorisations complémentaires",
    passN: false,
  },
];

const Acor = ({ classes, elem, Thead, Validation }) => {
  return (
    <Box paddingTop="1rem">
      <Accordion
        defaultExpanded={elem.extended ? true : false}
        classes={{ root: classes.acoroot }}
      >
        <AccordionSummary
          expandIcon={
            <ExpandMoreIcon style={{ color: theme.palette.inputcolor.text }} />
          }
          aria-controls="panel1a-content"
          id="panel1a-header"
          classes={{
            root: classes.sumroot,
            content: classes.sumcontent,
            expandIconWrapper: classes.iconwraper,
          }}
        >
          <Typography className={classes.acorHead}>{elem.Head}</Typography>
          {elem.passN ? (
            <Box className={classes.bacorheadn}>
              <Typography className={classes.acorHeadN}>N°0324</Typography>
            </Box>
          ) : (
            false
          )}
        </AccordionSummary>
        <AccordionDetails>
          <PutTable rows={step2rows} Thead={Thead} Validation={Validation}/>
        </AccordionDetails>
      </Accordion>
    </Box>
  );
};

const Etape1 = ({Thead, Validation}) => {
  const classes = useStyles();
  return (
    <Box paddingBottom="2rem">
      {acors.map((elem, index) => (
        <Acor classes={classes} elem={elem} key={index} Thead={Thead} Validation={Validation}/>
      ))}
    </Box>
  );
};

export default Etape1;
