const MainAcor = (props) => {
  const {
    children,
    classes,
    elem,
    NextStepindex,
    elemid,
    handleChange,
    expanded,
    Validation,
  } = props;
  return (
    <Box paddingBottom="1rem">
      <Accordion
        defaultExpanded={"E" + NextStepindex === elemid ? true : false}
        expanded={expanded === elemid}
        onChange={handleChange(elemid)}
        classes={{ root: classes.Mainacoroot }}
      >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon className={classes.MainAcorIcon} />}
          aria-controls="panel1a-content"
          id="panel1a-header"
          classes={{
            root: classes.Mainsumroot,
            content: classes.Mainsumcontent,
            expandIconWrapper: classes.Mainiconwraper,
          }}
        >
          <Box padding="29px" className={classes.conhead}>
            {"E" + NextStepindex === elemid && !Validation ? (
              false
            ) : (
              <CheckCircleIcon className={classes.sucssesicon} />
            )}

            <Box>
              <Typography className={classes.Tsubhead}>
                {elem.Subhead}
              </Typography>
              <Typography className={classes.Thead}>{elem.Head}</Typography>
            </Box>
          </Box>
        </AccordionSummary>
        {/* <AccordionDetails>{children}</AccordionDetails> */}
        <AccordionDetails>
          <GetEtape {...props} />
        </AccordionDetails>
      </Accordion>
    </Box>
  );
};

const GetEtape = (props) => {
  const { elemid } = props;
  const intid = parseInt(elemid[1], 10);
  if (intid === 0) return <Etape1 {...props} />;
  if (intid >= 1 && intid <= 2) return <Etape2and3 {...props} />;
  if (intid >= 3) return <Etape4 {...props} />;
};

const Step2map = ({
  NextStep,
  classes,
  NextStepindex,
  expanded,
  handleChange,
  Validation,
  alltickets,
}) => {
  return NextStep.map((elem) => (
    <MainAcor
      elem={elem.Header}
      classes={classes}
      key={elem.id}
      elemid={elem.id}
      NextStepindex={NextStepindex}
      expanded={expanded}
      handleChange={handleChange}
      Validation={Validation}
      alltickets={alltickets}
      Thead={elem.Thead}
      rows={elem.rows}
      Searchinputs={elem.Searchinputs}
      noHinput={elem.noHinput}
    />
  ));
};

const Step2 = ({
  handleBack,
  handleNext,
  NextStep,
  setNextStep,
  NextStepindex,
  setNextStepindex,
  alltickets,
  Validation,
  DefaultObj,
}) => {
  

  const [expanded, setExpanded] = useState("E0");
  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };
  const classes = useStyles();
  const ftNextStep = () => {
    if (NextStepindex < 6) {
      setNextStep([...NextStep, etaps[NextStepindex]]);
      setExpanded(etaps[NextStepindex].id);
    } else setExpanded(false);
    setNextStepindex(NextStepindex + 1);
  };
  return (
    <>
      <Step2map
        NextStep={NextStep}
        classes={classes}
        NextStepindex={NextStepindex}
        expanded={expanded}
        handleChange={handleChange}
        Validation={Validation}
        alltickets={alltickets}
      />
      <Buttons
        align="right"
        contwidth="50%"
        butns={butns}
        functions={{
          handleBack: handleBack,
          handleNext: NextStepindex <= 6 ? ftNextStep : handleNext,
        }}
      />
    </>
  );
};

export default Step2;
