import { Stepper, Step, StepLabel } from "@mui/material";
import { React, useEffect } from "react";
import { ThemeProvider } from "@mui/material/styles";
import { useStep } from "react-hooks-helper";
import { useStyles } from "../../Styles/PermiStyles";
import { Switcher } from "./Stepper";
import { QontoConnector, MySteplabel } from "./StepperLabel";
import { theme } from "../Theme";
import { useState } from "react";
import Etape1 from "./PermisSteps/Step2/Etap1";
import { etaps } from "./PermisSteps/Step2/Step2";
function Permis(props) {
  const steps = [
    { id: "Création du Formulaire", indx: 0 },
    { id: "Exigences de Pénétration", indx: 1 },
    { id: "Vérification Physique", indx: 2 },
    { id: "Validation et signature", indx: 3 },
  ];
  const { step, navigation } = useStep({
    steps,
    initialStep: 0,
  });

  const DefaultObj = {
    id: "E0",
    Header: {
      Subhead: "Etape 1",
      Head: "Avant la pénétration dans l’espace confiné",
      extended: true,
    },
    Thead:["Actions", "Commentaires"],
    // children: <Etape1 Thead={["Actions", "Commentaires"]} />,
  };
  const DefaultRev = {
    id: "E0",
    Header: {
      Subhead: "Etape 1",
      Head: "Avant la pénétration dans l’espace confiné",
      extended: true,
    },
    Thead:["Actions", "Commentaires"],
    // children: <Etape1 Thead={["Actions", "Commentaires"]}  />,
  };


  const handleNext = () => {
    navigation.next();
    setNextStepindex(step.indx >= 1 ? 7 : 0);
    window.scroll(0, 0);
  };
  const handleBack = () => {
    navigation.previous();
    window.scroll(0, 0);
  };
  const [NextStepindex, setNextStepindex] = useState(0);
  const [NextStep, setNextStep] = useState([DefaultObj]);
  const [Validstep, setValidstep] = useState([DefaultRev, ...etaps]);
  // const Steps = useRef([]);
  const classes = useStyles();
  return (
    <div className={props.className}>
      <ThemeProvider theme={theme}>
        <Stepper
          activeStep={step.indx}
          alternativeLabel
          connector={<QontoConnector theme={theme} />}
          className={classes.stepper}
        >
          {steps.map((label, index) => (
            <Step key={index}>
              <StepLabel
                StepIconComponent={MySteplabel}
                StepIconProps={{ label: label.id }}
              />
            </Step>
          ))}
        </Stepper>
        <Switcher
          step={step}
          NextStepindex={NextStepindex}
          setNextStepindex={setNextStepindex}
          NextStep={NextStep}
          setNextStep={setNextStep}
          Validstep={Validstep}
          setValidstep={setValidstep}
          handleNext={handleNext}
          handleBack={handleBack}
        />
      </ThemeProvider>
    </div>
  );
}

export default Permis;
