import { Box } from "@mui/system";
import { Grid, Typography } from "@mui/material";
import uuid from "react-uuid";

const Head = ({ classes, Thead }) => {
  return (
    <Grid container style={{ margin: "1rem" }} >
      <Grid item xs={1} >
        <Box></Box>
      </Grid>
      <Grid item xs={5} >
        <Box align="left">
          <Typography align="left" className={classes.Thead}>
            {Thead[0]}
          </Typography>
        </Box>
      </Grid>
      <Grid item>
        <Box align="left">
          <Typography align="left" className={classes.Thead}>
            {Thead[1]}
          </Typography>
        </Box>
      </Grid>
    </Grid>
  );
};
export default Head;
