import EditOutlinedIcon from "@mui/icons-material/EditOutlined";
import DescriptionOutlinedIcon from "@mui/icons-material/DescriptionOutlined";
import AssignmentTurnedInOutlinedIcon from "@mui/icons-material/AssignmentTurnedInOutlined";
import CheckCircleOutlinedIcon from "@mui/icons-material/CheckCircleOutlined";
import clsx from "clsx";
import { withStyles, styled } from "@mui/styles";
import { useTheme } from "@mui/material/styles";
import { StepConnector, Typography } from "@mui/material";
import { useStyles } from "../../Styles/PermiStyles";
import { theme } from "../Theme";

export const QontoConnector = withStyles(
  {
    alternativeLabel: {
      top: "50%",
      left: "calc(-50% + 16px)",
      right: "calc(50% + 16px)",
      zIndex: 1,
    },
    active: {
      "& $line": {
        borderColor: theme.palette.primary.main,
        borderTopWidth: "4px",
      },
    },
    completed: {
      "& $line": {
        borderColor: theme.palette.primary.main,
        borderTopWidth: "4px",
      },
    },
    line: {
      borderColor: "#eaeaf0",
      borderTopWidth: 0,
      borderRadius: 1,
    },
  },
  { withTheme: true }
)(StepConnector);

const MySteplabelroot = styled("div")(({ ownerState, theme }) => ({
  backgroundColor: "#FFFFFF",
  opacity: "30%",
  zIndex: 1,
  color: "#fff",
  width: "186px",
  border: "2px solid #FFFFFF",
  height: "76px",
  display: "flex",
  flexDirection: "column",
  justifyContent: "space-evenly",
  alignItems: "center",
  borderRadius: "6px",
  ...(ownerState.active && {
    opacity: "100%",
    border: "2px solid #7BCB09",
  }),
  ...(ownerState.completed && {
    border: "2px solid #7BCB09",

    backgroundColor: theme.palette.primary.main,
    opacity: "100%",
  }),
}));

export function MySteplabel(props) {
  const theme = useTheme();
  const classes = useStyles();
  const { active, completed, className, label } = props;
  const Labelclass = clsx(
    classes.steplabel,
    active && classes.slactive,
    completed && classes.slcompleted
  );
  const Iconclass = clsx(
    classes.stepicon,
    active && classes.slactive,
    completed && classes.slcompleted
  );
  const icons = {
    1: <EditOutlinedIcon className={Iconclass} />,
    2: <DescriptionOutlinedIcon className={Iconclass} />,
    3: <AssignmentTurnedInOutlinedIcon className={Iconclass} />,
    4: <CheckCircleOutlinedIcon className={Iconclass} />,
  };

  return (
    <MySteplabelroot
      ownerState={{ completed, active }}
      className={className}
      theme={theme}
    >
      {icons[String(props.icon)]}
      <Typography variant="caption" component="span" className={Labelclass}>
        {label}
      </Typography>
    </MySteplabelroot>
  );
}
