import { Button, Grid } from "@mui/material";
import { Box } from "@mui/system";

const getFuction = (name, functions) => {
  if (name === "next") return functions.handleNext;
  else if (name === "back") return functions.handleBack;
  else return undefined;
};

const Mapbutns = ({ butns, functions }) => {
  return butns.map((butn) => {
    const butnfunc = getFuction(butn.func, functions);
    return (
      <Grid item xs={butn.col} key={butn.id}>
        <Button
          variant={butn.type}
          fullWidth
          style={butn.style}
          onClick={butnfunc}
        >
          {butn.name}
        </Button>
      </Grid>
    );
  });
};

const Buttons = ({ align, contwidth, butns, functions }) => {
  return (
    <Box
      width={contwidth}
      marginLeft={align === "right" ? "auto" : 0}
      paddingBottom="1rem"
    >
      <Grid container spacing={2}>
        <Mapbutns butns={butns} functions={functions} />
      </Grid>
    </Box>
  );
};

export default Buttons;
