import * as React from "react";
import TextField from "@mui/material/TextField";
import Dialog from "@mui/material/Dialog";
import DialogTitle from "@mui/material/DialogTitle";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogActions from "@mui/material/DialogActions";
import Button from "@mui/material/Button";
import Autocomplete, { createFilterOptions } from "@mui/material/Autocomplete";
import { Grid, Typography } from "@mui/material";
import { useStyles } from "./Step2Styles";
import { SearchField } from "../../../../tools/Table/Cositems";
import uuid from "react-uuid";
const filter = createFilterOptions();

export default function FreeSoloCreateOptionDialog({
  settickets,
  tickets,
  alltickets,
  Searchinputs,
}) {
  const classes = useStyles();
  const [value, setValue] = React.useState(null);
  const [open, toggleOpen] = React.useState(false);

  const handleClose = () => {
    setDialogValue([]);

    toggleOpen(false);
  };

  const [dialogValue, setDialogValue] = React.useState([]);

  const Onchangetext = (NewValue, index) => {
    const NewValues = dialogValue;
    NewValues[index] = NewValue;
    setDialogValue(NewValues);
  };
  const handleSubmit = (event) => {
    event.preventDefault();
    // setValue({
    //   title: dialogValue.title,
    //   num: dialogValue.num,
    // });
    settickets([...tickets, [uuid(), dialogValue]]);
    alltickets.current = [...tickets, [uuid(), dialogValue]];
    handleClose();
  };

  const Ff = {
    "& .MuiOutlinedInput-root": {
      borderRadius: "6px 0px 0px 6px",
    },
  };
  const Mf = {
    "& .MuiOutlinedInput-root": {
      borderRadius: "0px 0px 0px 0px",
    },
  };
  const Lf = {
    "& .MuiOutlinedInput-root": {
      borderRadius: "0px 6px 6px 0px",
    },
  }

  return (
    <React.Fragment>
      <Autocomplete
        value={value}
        onChange={(event, newValue) => {
          if (typeof newValue === "string") {
            // timeout to avoid instant validation of the dialog's form.
            setTimeout(() => {
              toggleOpen(true);
              setDialogValue([newValue]);
            });
          } else if (newValue && newValue.inputValue) {
            toggleOpen(true);
            setDialogValue([newValue.inputValue]);
          } else {
            setValue(newValue);
          }
        }}
        filterOptions={(options, params) => {
          const filtered = filter(options, params);

          if (params.inputValue !== "") {
            filtered.push({
              inputValue: params.inputValue,
              title: `Add "${params.inputValue}"`,
            });
          }

          return filtered;
        }}
        id={uuid()}
        options={top100Films}
        getOptionLabel={(option) => {
          // e.g value selected with enter, right from the input
          if (typeof option === "string") {
            return option;
          }
          if (option.inputValue) {
            return option.inputValue;
          }
          return option.title;
        }}
        selectOnFocus
        clearOnBlur={false}
        handleHomeEndKeys
        renderOption={(props, option) => <li {...props}>{option.title}</li>}
        sx={{
          "& .MuiAutocomplete-popupIndicatorOpen": {
            transform: "none !important",
          },
          "& .MuiAutocomplete-endAdornment": {
            top: "calc(40% - 14px) !important",
          },
        }}
        freeSolo
        forcePopupIcon
        popupIcon={
          <Button
            variant="contained"
            onClick={() => {
              toggleOpen(true);
            }}
          >
            Créer
          </Button>
        }
        renderInput={(params) => (
          <SearchField
            classes={{ root: classes.troot }}
            label="Nom et prénom *"
            fullWidth
            {...params}
          />
        )}
      />
      <Dialog open={open} onClose={handleClose} maxWidth="md" fullWidth>
        <form onSubmit={handleSubmit}>
          <DialogTitle>
            <Typography className={classes.Thead} paragraph>
              Créer Téléphone en cas d'urgence
            </Typography>
          </DialogTitle>
          <DialogContent
            classes={{
              root: classes.dialogcontent,
            }}
          >
            <Grid container>
              {Searchinputs.map((elem, index) => (
                <Grid item xs={12} md={elem.md} key={index}>
                  <SearchField
                    sx={index === 0 ? Ff : index + 1 === (Searchinputs.length) ? Lf : Mf}
                    autoFocus
                    fullWidth
                    margin="dense"
                    id={elem.value}
                    value={dialogValue[index]}
                    onChange={(event) =>
                      Onchangetext(event.target.value, index)
                    }
                    label={elem.name}
                    type="text"
                    variant="outlined"
                  />
                </Grid>
              ))}
             
            </Grid>
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose}>Cancel</Button>
            <Button type="submit">Add</Button>
          </DialogActions>
        </form>
      </Dialog>
    </React.Fragment>
  );
}

// Top 100 films as rated by IMDb users. http://www.imdb.com/chart/top
const top100Films = [];
