import { Switch, TextField } from "@mui/material";
import { styled } from "@mui/material/styles";
import { Box } from "@mui/system";

export const IOSSwitch = styled((props) => (
  <Switch focusVisibleClassName=".Mui-focusVisible" disableRipple {...props} />
))(({ theme }) => ({
  width: "32px",
  height: "21px",
  padding: 0,
  "& .MuiSwitch-switchBase": {
    padding: 0,
    margin: 2,
    transitionDuration: "300ms",
    transform: "translateY(0px)",
    "&.Mui-checked": {
      transform: "translate(11px, 0px)",
      color: "#fff",
      "& + .MuiSwitch-track": {
        backgroundColor: theme.palette.primary.main,
        opacity: 1,
        border: 0,
      },
      "&  .MuiSwitch-thumb": {
        backgroundSize: "cover",
        backgroundImage: `url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" height="16" width="16" viewBox="0 0 24 24"><path fill="${encodeURIComponent(
          theme.palette.primary.main
        )}" d="M21,7L9,19L3.5,13.5L4.91,12.09L9,16.17L19.59,5.59L21,7Z"/></svg>')`,
      },
      "&.Mui-disabled + .MuiSwitch-track": {
        opacity: 0.5,
      },
    },
    "&.Mui-focusVisible .MuiSwitch-thumb": {
      color: "#33cf4d",
      border: "6px solid #fff",
    },
    "&.Mui-disabled .MuiSwitch-thumb": {
      color:
        theme.palette.mode === "light"
          ? theme.palette.grey[100]
          : theme.palette.grey[600],
    },
    "&.Mui-disabled + .MuiSwitch-track": {
      opacity: theme.palette.mode === "light" ? 0.7 : 0.3,
    },
  },
  "& .MuiSwitch-thumb": {
    boxSizing: "border-box",
    width: 17,
    height: 17,
    backgroundSize: "7px",
    backgroundRepeat: "no-repeat",
    backgroundPosition: "center",
    backgroundImage: `url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="5" height="5" viewBox="0 0 5 5" overflow="visible" stroke="${encodeURIComponent(
      "#FF3F69"
    )}" stroke-width="1" stroke-linecap="round"><line x2="5" y2="5" /><line x1="5" y2="5" /></svg>')`,
  },
  "& .MuiSwitch-track": {
    borderRadius: 26 / 2,
    backgroundColor: "#FF3F69",
    opacity: 1,
    transition: theme.transitions.create(["background-color"], {
      duration: 500,
    }),
  },
}));

export const CssTextField = styled(TextField)(({ theme }) => ({
  "& label , input": {
    opacity: "0.8",
    color: theme.palette.inputcolor.text,
    fontFamily: "Roboto",
    fontSize: "14px",
    letterSpacing: "0",
    lineHeight: "16px",
  },

  "& .MuiInput-underline:hover": {
    border: 0,
  },
  "& .MuiInput-underline:after": {
    border: 0,
  },
  "&:hover .MuiInput-underline:before": {
    border: "0 !important",
  },
  "& .MuiInput-underline:before": {
    border: 0,
  },
  "&:hover .MuiInputBase-input": {
    border: 0,
  },
}));

export const StyledTableRow = styled(Box)(({ theme }) => ({
  border: "1px solid" + theme.palette.inputcolor.border,
  borderRadius: "6px",
  margin: "1rem",
  height: "54px",
  display: "flex",
  alignItems: "center",
}));
export const StyledTableRowgreen = styled(Box)(({ theme }) => ({
  border: "1px solid" + theme.palette.inputcolor.border,
  borderRadius: "6px",
  margin: "1rem",
  height: "54px",
  display: "flex",
  alignItems: "center",
  backgroundColor: "#F5FFE7",
}));
export const StyledTableRowred = styled(Box)(({ theme }) => ({
  border: "1px solid" + theme.palette.inputcolor.border,
  borderRadius: "6px",
  margin: "1rem",
  height: "54px",
  display: "flex",
  alignItems: "center",
  backgroundColor: "rgba(255,63,105,0.05)",
}));

export const SearchField = styled(TextField)(({ theme }) => ({
  "& label , input": {
    opacity: "0.8",
    color: theme.palette.inputcolor.text,
    fontFamily: "Roboto",
    fontSize: "14px",
    letterSpacing: "0",
    lineHeight: "16px",
  },

  "& .MuiOutlinedInput-root": {
    borderRadius: "6px",
    borderColor: theme.palette.inputcolor.border,
  },
}));

export const CosField = styled(TextField)(({ theme }) => ({
  "& label , input": {
    color: theme.palette.inputcolor.text,
    fontFamily: "Roboto",
    fontSize: "16px",
    fontWeight: "500",
    letterSpacing: "0",
    lineHeight: "19px",
  },
  "& label": {
    opacity: 0.8,
  },
  "& .MuiOutlinedInput-root": {
    borderRadius: "6px",
    borderColor: theme.palette.inputcolor.border,
  },
}));
