import { useStyles } from "../../../Styles/StepsStyles";
import { Autorisation, CreatForm } from "./Step1";
import Step2 from "./Step2/Step2";
const ValidStep = ({
  addpass,
  setAddpass,
  handleBack,
  handleNext,
  NextStep,
  setNextStep,
  NextStepindex,
  setNextStepindex,
  alltickets,
  Validation,
}) => {
  const classes = useStyles();

  return (
    <>
      <CreatForm classes={classes} Validation={Validation} />
      {addpass.map((pass) => (
        <Autorisation
          id={pass}
          classes={classes}
          key={pass}
          addpass={addpass}
          setAddpass={setAddpass}
        />
      ))}
      <Step2
        handleNext={handleNext}
        handleBack={handleBack}
        NextStep={NextStep}
        setNextStep={setNextStep}
        NextStepindex={NextStepindex}
        setNextStepindex={setNextStepindex}
        alltickets={alltickets}
        Validation={true}
        // DefaultObj={DefaultObj}
      />
    </>
  );
};
export default ValidStep;
