import * as React from "react";
import { Box } from "@mui/system";
import { Grid, TextField, Typography } from "@mui/material";
import { makeStyles } from "@mui/styles";
import { theme } from "../../forms/Theme";
import { useState } from "react";
import Head from "./TableHead";
import {
  IOSSwitch,
  CssTextField,
  StyledTableRow,
  StyledTableRowgreen,
  StyledTableRowred,
} from "./Cositems";
export const useStyles = makeStyles({
  Thead: {
    color: theme.palette.secondary.main,
    fontFamily: "Roboto",
    fontSize: "12px",
    letterSpacing: "0",
    lineHeight: "14px",
  },
  Tcell: {
    color: theme.palette.inputcolor.text,
    fontFamily: "Roboto",
    fontSize: "14px",
    fontWeight: "500",
    letterSpacing: "0",
    lineHeight: "16px",
  },
  green: {
    border: "1px solid #7BCB09",
    borderRadius: "6px",
    margin: "1rem",
    height: "54px",
    display: "flex",
    alignItems: "center",
    backgroundColor: "#F5FFE7",
  },
  red: {
    border: "1px solid #FF3F69" ,
    borderRadius: "6px",
    margin: "1rem",
    height: "54px",
    display: "flex",
    alignItems: "center",
    backgroundColor: "rgba(255,63,105,0.05)",
  },
});

const OneRow = ({ classes, row, Validation, setcheck, index, check }) => {
  return (
    <Grid container alignItems="center">
      <Grid item xs={1}>
        <Box display="flex" justifyContent="center">
          <IOSSwitch
            // defaultChecked={Validation ? false : true}
            checked={check[index] !== false  ? true : false}
            onChange={(event) => {
              const data = check;
              if (event.target.checked) data[index] = true;
              else data[index] = false;
              setcheck([...data]);
            }}
          />
        </Box>
      </Grid>
      <Grid item xs={5}>
        <Typography align="left" className={classes.Tcell}>
          {row.name}
        </Typography>
      </Grid>
      <Grid item xs={6}>
        {row.textfield ? (
          <CssTextField
            variant="standard"
            label="Ajouter votre commentaire ici"
            fullWidth
          />
        ) : (
          <Typography align="left" className={classes.Tcell}>
            {row.label}
          </Typography>
        )}
      </Grid>
    </Grid>
  );
};

export default function PutTable({ rows, Thead, Validation }) {
  const [check, setcheck] = useState([...rows]);
  const classes = useStyles();
  return (
    <Box sx={{ minWidth: 650 }} aria-label="simple table">
      <Head classes={classes} Thead={Thead} />
      {Validation
        ? rows.map((row, index) => (
            <Box
              key={index}
              className={check[index] !== false ? classes.green : classes.red}
              children={
                <OneRow
                  classes={classes}
                  row={row}
                  setcheck={setcheck}
                  check={check}
                  index={index}
                  Validation={Validation}
                />
              }
            />
          ))
        : rows.map((row, index) => (
            <StyledTableRow
              key={index}
              children={
                <OneRow
                  classes={classes}
                  row={row}
                  setcheck={setcheck}
                  check={check}
                  index={index}
                />
              }
            />
          ))}
      {/* {rows.map((row, index) => (
        <StyledTableRow key={index} children={<OneRow classes={classes} row={row} />}/>
      ))} */}
    </Box>
  );
}
