import { Button, IconButton, Paper, Popover, Typography } from "@mui/material";
import { useState, useEffect } from "react";
import { Box } from "@mui/system";
import { useStyles } from "../../../Styles/StepsStyles";
import {
  s1inputs2,
  s1inputs1,
  s1inputs3,
  s1inputs4,
  butns,
} from "../../../inputs/step1inputs";
import Putinputs from "../../../tools/Inputs/Putinputs";
import { theme } from "../../Theme";
import AddIcon from "@mui/icons-material/Add";
import Buttons from "../../../tools/Buttons/Buttons";
import MoreVertIcon from "@mui/icons-material/MoreVert";
import DeleteIcon from "@mui/icons-material/Delete";
import FileCopyIcon from "@mui/icons-material/FileCopy";
import uuid from "react-uuid";
export const CreatForm = ({ classes, Validation }) => {
  return (
    <Paper style={{ marginBottom: theme.spacing(2) }}>
      <Box padding="2rem">
        {Validation ? (
          false
        ) : (
          <>
            <Typography className={classes.Thead} paragraph>
              Création du Formulaire Permis de pénétration
            </Typography>
            <Typography className={classes.Tsubhead} paragraph>
              Afin d’ajouter un nouveau permis de pénétration, vous devez
              d'abord remplir ces informations.
            </Typography>
          </>
        )}

        <Box paddingTop={Validation ? 0 : "2rem"}>
          <Putinputs inputs={s1inputs1} grid={true} />
          <Putinputs inputs={s1inputs2} />
        </Box>
      </Box>
    </Paper>
  );
};

function Mypopover({
  anchorEl,
  handleClose,
  classes,
  addpass,
  setAddpass,
  id,
}) {
  const open = Boolean(anchorEl);
  const pid = open ? "simple-popover" : undefined;

  const deletepass = () => {
    const newpasses = addpass.filter((elem) => {
      return elem !== id;
    });
    setAddpass(newpasses);
  };
  return (
    <Popover
      id={pid}
      classes={{ paper: classes.poproot }}
      open={open}
      anchorEl={anchorEl}
      onClose={handleClose}
      anchorOrigin={{
        vertical: "bottom",
        horizontal: "left",
      }}
    >
      <Button
        startIcon={<DeleteIcon />}
        fullWidth
        size="large"
        className={classes.popbut}
        onClick={deletepass}
      >
        Supprimer
      </Button>
      <Button
        startIcon={<FileCopyIcon />}
        fullWidth
        size="large"
        className={classes.popbut}
        // onClick={ftAddpass}
      >
        Dupliquer
      </Button>
    </Popover>
  );
}

export const Autorisation = (props) => {
  const [anchorEl, setAnchorEl] = useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
  return (
    <Paper
      style={{
        marginBottom: theme.spacing(2),
        position: "relative",
      }}
    >
      <Box padding="2rem">
        <Putinputs inputs={s1inputs3} grid={true} />
        <Putinputs inputs={s1inputs4} />
      </Box>
      <Box className={props.classes.boxbut}>
        <IconButton onClick={handleClick}>
          <MoreVertIcon style={{ color: "white" }} />
        </IconButton>
      </Box>
      <Mypopover handleClose={handleClose} anchorEl={anchorEl} {...props} />
    </Paper>
  );
};

const AddPass = ({ classes, ftAddpass }) => {
  return (
    <Paper>
      <Box marginBottom={theme.spacing(3)}>
        <Button
          startIcon={<AddIcon />}
          fullWidth
          size="large"
          className={classes.but}
          onClick={ftAddpass}
        >
          Ajouter une autre autorisation
        </Button>
      </Box>
    </Paper>
  );
};

const Step1 = ({ handleBack, handleNext, addpass, setAddpass }) => {
  const classes = useStyles();
  const ftAddpass = () => {
    setAddpass([...addpass, "Pass" + uuid()]);
  };

  return (
    <>
      <CreatForm classes={classes} />
      {addpass.map((pass) => (
        <Autorisation
          id={pass}
          classes={classes}
          key={pass}
          addpass={addpass}
          setAddpass={setAddpass}
        />
      ))}
      <AddPass classes={classes} ftAddpass={ftAddpass} />
      <Buttons
        align="right"
        contwidth="50%"
        butns={butns}
        functions={{ handleBack: handleBack, handleNext: handleNext }}
      />
    </>
  );
};

export default Step1;
