import { Paper, Typography, Button } from "@mui/material";
import Step1 from "./PermisSteps/Step1";
import Step2 from "./PermisSteps/Step2/Step2";

import { useState, useEffect, useRef } from "react";
import Etape1 from "./PermisSteps/Step2/Etap1";
import uuid from "react-uuid";
import ValidStep from "./PermisSteps/ValidStep";

export const Switcher = ({
  step,
  handleBack,
  handleNext,
  NextStepindex,
  setNextStepindex,
  NextStep,
  setNextStep,
  setValidstep,
  Validstep,
}) => {
  const [addpass, setAddpass] = useState(["Pass" + uuid()]);

  const alltickets = useRef([]);
  switch (step.indx) {
    case 0:
      return (
        <Step1
          handleNext={handleNext}
          handleBack={handleBack}
          addpass={addpass}
          setAddpass={setAddpass}
        />
      );
    case 1:
      return (
        <Step2
          handleNext={handleNext}
          handleBack={handleBack}
          NextStep={NextStep}
          setNextStep={setNextStep}
          NextStepindex={NextStepindex}
          setNextStepindex={setNextStepindex}
          alltickets={alltickets}
          // settickets={settickets}
        />
      );
    case 2:
      return (
        <Step2
          handleNext={handleNext}
          handleBack={handleBack}
          NextStep={Validstep}
          setNextStep={setValidstep}
          NextStepindex={NextStepindex}
          setNextStepindex={setNextStepindex}
          alltickets={alltickets}
          Validation={true}
          // DefaultObj={DefaultObj}
        />
      );
    case 3:
      return (
        // <Paper>
        <ValidStep
          addpass={addpass}
          setAddpass={setAddpass}
          handleNext={handleNext}
          handleBack={handleBack}
          NextStep={Validstep}
          setNextStep={setValidstep}
          NextStepindex={NextStepindex}
          setNextStepindex={setNextStepindex}
          alltickets={alltickets}
          Validation={true}
        />
        // </Paper>
      );
    default:
  }
};
