import { makeStyles } from "@mui/styles";
import MapInputs from "./mapInputs";
import { theme } from "../../forms/Theme";

const useStyles = makeStyles(() => ({
  form: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    width: "100%",
    alignItems: "center",
    "& > *": {
      margin: theme.spacing(1),
      width: "100%",
    },
  },
  griding_last_child_nth_child_3n___2: { gridColumn: "span 3" },
  griding: {
    display: "grid",
    gridTemplateColumns: "repeat(1, 1fr)",
    [theme.breakpoints.up("sm")]: {
      gridTemplateColumns: "repeat(3, 1fr)",
    },
    columnGap: theme.spacing(2),
    rowGap: theme.spacing(3),
    flexWrap: "wrap",
    paddingBottom: theme.spacing(1),
    // paddingTop: theme.spacing(3),
    gridColumn: "span 3"
  },
}));
function PutInputs(props) {
  const classes = useStyles();
  const { inputs } = props;
  return (
    <div className={props.grid ? classes.griding : classes.form}>
      {inputs.map((input) => {
        return (
          <MapInputs
            key={input.id + 100}
            input={input}
            {...props}
          />
        );
      })}
    </div>
  );
}
export default PutInputs;
