import PutTable from "../../../../tools/Table/PutTable";
import { Box } from "@mui/system";
import SearchAdd from "./SearchAdd";
import { Grid, Typography } from "@mui/material";
import { useStyles } from "./Step2Styles";
import { CosField } from "../../../../tools/Table/Cositems";
import { useState } from "react";
import HighlightOffIcon from "@mui/icons-material/HighlightOff";
import { IconButton } from "@mui/material";
const Etape2and3 = ({ rows, Thead, Validation }) => {
  //   const classes = useStyles();
  return (
    <Box paddingBottom="2rem">
      <PutTable rows={rows} Thead={Thead} Validation={Validation} />
    </Box>
  );
};

const Head = ({ noHinput, classes, Validation }) => {
  return noHinput ? (
    false
  ) : (
    <>
      <Box paddingBottom="2rem">
        <CosField
          label="Plan de secours *"
          fullWidth
          disabled={Validation ? true : false}
        />
      </Box>
      {Validation ? (
        false
      ) : (
        <Typography className={classes.Lhead} paragraph>
          Téléphone en cas d'urgence
        </Typography>
      )}
    </>
  );
};

const TableHead = ({ Thead, classes, Searchinputs }) => {
  return Thead ? (
    <Box className={classes.ticketheader}>
      <Grid container alignItems="center">
        {Thead.map((elem, index) => (
          <Grid
            key={index}
            item
            xs={
              Thead.length > index + 1
                ? Searchinputs[index].md
                : Searchinputs[index].md - 1
            }
          >
            <Typography className={classes.titles} key={index}>
              {elem}
            </Typography>
          </Grid>
        ))}
      </Grid>
    </Box>
  ) : (
    false
  );
};

export const Etape4 = ({
  rows,
  Thead,
  alltickets,
  noHinput,
  Searchinputs,
  Validation,
}) => {
  const [tickets, settickets] = useState(alltickets.current);

  const deletetickets = (id) => {
    const newtickets = tickets.filter((elem) => {
      return elem[0] !== id;
    });
    settickets(newtickets);
    alltickets.current = newtickets;
  };
  const classes = useStyles();
  return (
    <Box paddingBottom="2rem" margin="1rem">
      <Head classes={classes} noHinput={noHinput} Validation={Validation} />
      {Validation ? (
        false
      ) : (
        <SearchAdd
          settickets={settickets}
          tickets={tickets}
          deletetickets={deletetickets}
          alltickets={alltickets}
          Searchinputs={Searchinputs}
        />
      )}

      <TableHead classes={classes} Thead={Thead} Searchinputs={Searchinputs} />
      {tickets.map((elem, index) => (
        <Box className={classes.ticketcont} key={index}>
          <Grid container alignItems="center">
            {elem[1].map((data, index) => (
              <Grid
                item
                xs={
                  elem[1].length > index + 1
                    ? Searchinputs[index].md
                    : Searchinputs[index].md - 1
                }
              >
                <Typography className={classes.tickettext} key={index}>
                  {data}
                </Typography>
              </Grid>
            ))}
            <IconButton
              onClick={() => {
                deletetickets(elem[0]);
              }}
            >
              <HighlightOffIcon style={{ color: "red" }} />
            </IconButton>
          </Grid>
        </Box>
      ))}
    </Box>
  );
};

export default Etape2and3;
